//////////////////////
// group
//////////////////////

.group {
  .link--none();
  .display(inline-flex) !important;
  .flex--nowrap();
  position: relative;
  padding: 0 !important;
  // .form_input
  .form_input {
    .flex--auto();
    width: auto;
    // border
    &:first-child {
      border-top-right-radius: 0 !important;
      border-bottom-right-radius: 0 !important;
    }
    &:last-child {
      border-top-left-radius: 0 !important;
      border-bottom-left-radius: 0 !important;
    }
    &:not(:first-child):not(:last-child) {
      border-radius: 0 !important;
    }
  }
}

// .group_inner

.group_inner {
  .display(inline-flex);
  .flex-direction--column();
  .flex--wrap();
  // .btn
  > * {
    .flex--auto();
    .text-align(center);
    white-space: nowrap;
    + * {
      margin-top: -@btn--border-width;
    }
  }
  .form_input ~ & {
    > * {
      .margin(right, none);
    }
  }
  // border inline
  &:first-child {
    > * {
      &@{btn--design} {
        border-top-right-radius: 0 !important;
        border-bottom-right-radius: 0 !important;
      }
    }
  }
  &:last-child {
    > * {
      &@{btn--design} {
        border-top-left-radius: 0 !important;
        border-bottom-left-radius: 0 !important;
      }
    }
  }
  &:not(:first-child):not(:last-child) {
    > * {
      &@{btn--design} {
        border-radius: 0 !important;
      }
    }
  }
  // border stack
  &:first-child {
    > * {
      &:not(:first-child) {
        &@{btn--design} {
          border-bottom-right-radius: 0 !important;
          border-top-right-radius: 0 !important;
          border-top-left-radius: 0 !important;
        }
      }
      &:not(:last-child) {
        &@{btn--design} {
          border-top-right-radius: 0 !important;
          border-bottom-right-radius: 0 !important;
          border-bottom-left-radius: 0 !important;
        }
      }
      &:not(:first-child):not(:last-child) {
        &@{btn--design} {
          border-top-left-radius: 0 !important;
          border-bottom-left-radius: 0 !important;
        }
      }
    }
  }
  &:last-child {
    > * {
      &:not(:first-child) {
        &@{btn--design} {
          border-bottom-left-radius: 0 !important;
          border-top-left-radius: 0 !important;
          border-top-right-radius: 0 !important;
        }
      }
      &:not(:last-child) {
        &@{btn--design} {
          border-top-left-radius: 0 !important;
          border-bottom-left-radius: 0 !important;
          border-bottom-right-radius: 0 !important;
        }
      }
      &:not(:first-child):not(:last-child) {
        &@{btn--design} {
          border-top-right-radius: 0 !important;
          border-bottom-right-radius: 0 !important;
        }
      }
    }
  }
}

// .group--collapse

.group--collapse {
  .group_inner {
    margin-left: 0 !important;
    margin-right: -@input--border-width !important;
  }
  .form_input ~ .group_inner {
    margin-left: -@input--border-width !important;
    margin-right: 0 !important;
  }
}